.Layout {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.Layout main {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}
.MenuToggle {
    position: fixed;
    top: 40px;
    left: 40px;
    font-size: 20px;
    cursor: pointer;
    color: white;
    z-index: 100;
    transition: opacity, left .22s ease-in;
}

.MenuToggle:hover {
    opacity: .7;
}

.MenuToggle.open {
    left: 320px;
}
.Drawer {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 80%;
    max-width: 300px;
    padding: 20px 10px;
    box-sizing: border-box;
    background: white;
    z-index: 90;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    transition: -webkit-transform .22s ease-in;
    transition: transform .22s ease-in;
    transition: transform .22s ease-in, -webkit-transform .22s ease-in;
    opacity: 1;
}

.Drawer.closed {
    -webkit-transform: translateX(-300px);
            transform: translateX(-300px);
}

.Drawer ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.Drawer ul li {
    margin-bottom: 15px;
}

.Drawer ul li a {
    color: #363d54;
    font-size: 30px;
    text-decoration: none;
    background-color: white;
    position: relative;
    padding: 0 20px 10px 20px;
    transition: opacity .3s;
}

.Drawer ul li a:hover, .active {
    opacity: .7;
}
.BackDrop {
    z-index: 50;
    background: rgba(0, 0, 0, .7);
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.Quiz {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
    background: linear-gradient(90deg, #1a90b2 0%, #58c0e6 100%);
}

.Quiz h2 {
    color: white;
    margin-left: 10px;
}

.QuizWrapper {
    width: 600px;
}
.ActiveQuiz {
    padding: 20px;
    color: white;
    border: 2px solid white;
    border-radius: 5px;
    margin: 10px;
    box-sizing: border-box;
}

.Question {
    display: flex;
    justify-content: space-between;
}
.AnswersList {
    list-style: none;
    margin: 0;
    padding: 0;
}
.AnswerItem {
    border: 1px solid white;
    border-radius: 5px;
    padding: 5px 10px;
    margin-bottom: 5px;
    cursor: pointer;
}

.AnswerItem:hover {
    background: rgba(255, 255, 255, .2);
    transition: background .3s ease-in-out;
}

.AnswerItem.correct {
    background: rgba(161, 240, 69, .7);
}

.AnswerItem.wrong {
    background: rgba(240, 87, 108, .7);
}
.FinishedQuiz {
    padding: 20px;
    color: white;
    border: 2px solid white;
    border-radius: 5px;
    box-sizing: border-box;
    margin: 0 10px;
}

.FinishedQuiz ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.FinishedQuiz ul li i {
    margin-left: 10px;
}

.correct {
    color: rgba(161, 240, 69, .7);
}

.wrong {
    color: rgba(240, 87, 108, .7);
}
.Button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #ccc;
    color: #000;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
}

.Button:focus {
    outline: none;
}

.Button:active {
    box-shadow: inset 2px 2px 1px rgba(0, 0, 0, .3);
}

.Button.wrong {
    background: rgba(240, 87, 108, 1);
}

.Button.correct {
    background: rgba(161, 240, 69, 1);
}

.primary {
    background: #2884f6;
    color: #fff;
}

.Button:disabled {
    background: #ccc;
    color: #000;
    cursor: not-allowed;
}
.Loader {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}
.Loader div {
    display: inline-block;
    position: absolute;
    left: 6px;
    width: 13px;
    background: #fff;
    -webkit-animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
            animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.Loader div:nth-child(1) {
    left: 6px;
    -webkit-animation-delay: -0.24s;
            animation-delay: -0.24s;
}
.Loader div:nth-child(2) {
    left: 26px;
    -webkit-animation-delay: -0.12s;
            animation-delay: -0.12s;
}
.Loader div:nth-child(3) {
    left: 45px;
    -webkit-animation-delay: 0;
            animation-delay: 0;
}

.center {
    text-align: center;
}

@-webkit-keyframes lds-facebook {
    0% {
        top: 6px;
        height: 51px;
    }
    50%, 100% {
        top: 19px;
        height: 26px;
    }
}

@keyframes lds-facebook {
    0% {
        top: 6px;
        height: 51px;
    }
    50%, 100% {
        top: 19px;
        height: 26px;
    }
}

.Auth {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
    background: linear-gradient(270deg, #cef2fa 0%, #59bfef 100%);
}

.Auth > div {
    width: 100%;
    max-width: 600px;
    padding: 0 20px;
}

.Auth h1 {
    color: white;
    text-align: center;
}

.AuthForm {
    background: #eee;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);
    padding: 15px;
    border-radius: 5px;
}
.Input {
    margin-bottom: 15px;
}

.Input label {
    margin-bottom: 3px;
    padding: 0;
    display: block;
    font-weight: bold;
}

.Input input {
    display: block;
    box-sizing: border-box;
    border: 1px solid #bebebe;
    padding: 7px;
    margin: 0 0 5px;
    width: 100%;
    outline: none;
    transition: all 300ms ease-in-out;
}

.Input span {
    color: #f01f30;
    font-weight: bold;
    font-size: 12px;
}

.Input.invalid label {
    color: #f01f30;
}
.QuizCreator {
    display: flex;
    justify-content: center;
    padding-top: 50px;
    flex-grow: 1;
    width: 100%;
    background: linear-gradient(270deg, #f0613c 0%, #f0783c 100%);
}

.QuizCreator > div {
    width: 100%;
    max-width: 600px;
    padding: 0 20px;
}

.QuizCreator h1 {
    text-align: center;
}

.QuizCreator form {
    background: #eeeeee;
    border-radius: 5px;
    padding: 15px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);
}
.Select {
    margin-bottom: 15px;
}

.Select label {
    margin:  0 0 3px 0;
    padding: 0;
    font-weight: bold;
    display: block;
}

.Select select {
    display: block;
    box-sizing: border-box;
    border: 1px solid #bebebe;
    margin: 0 0 5px;
    height: 29px;
    width: 100%;
    outline: none;
    transition: all 300ms ease-in-out;
}
.QuizList {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
    background: linear-gradient(90deg, #f98355 0%, #f0576c 37%, #f79cbd 100%);
}

.QuizList h1 {
    color: #ffffff;
}

.QuizList ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.QuizList li {
    margin-bottom: 10px;
}

.QuizList li a{
    text-decoration: none;
    color: #ffffff;
}

.QuizList li a:hover {
    color: #363f54;
}
